<template>
  <div class="post-wrap">
    <!-- Tabs Nav -->
    <ul class="tab-nav-wrap" data-panel="psn-tabs">
        <li class="tab-nav active" data-tabs="section-pc">電腦版</li>
        <li class="tab-nav" data-tabs="section-mobile">手機版</li>
    </ul>

    <!-- Tabs Content -->
    <div class="tab-content-wrap fade-tab" data-panel="psn-tabs">
        <!-- Tab 1 start -->
        <div class="tab-content active" data-tabs="section-pc">
            <h3>電腦版</h3>

            <div class="device-wrap pc-dev-wrap" style="--device-w:1920; --device-h:1080;">
                <div class="dev-outter-wrap">
                    <div class="dev-inner-wrap">
                        <img src="@/assets/images/work/andmker_ebook/pc_index_1920.png" />
                    </div>
                </div>
            </div>

            <h4>Modal</h4>
            <div class="device-wrap pc-dev-wrap" style="--device-w:1920; --device-h:1080;">
                <div class="dev-outter-wrap">
                    <div class="dev-inner-wrap">
                        <img src="@/assets/images/work/andmker_ebook/pc_modal_1920.png" />
                    </div>
                </div>
            </div>

            <h4>Modal - Success</h4>
            <div class="device-wrap pc-dev-wrap" style="--device-w:1920; --device-h:960;">
                <div class="dev-outter-wrap">
                    <div class="dev-inner-wrap">
                        <img src="@/assets/images/work/andmker_ebook/pc_success_1920.png" />
                    </div>
                </div>
            </div>
        </div>
        <!-- Tab 1 end -->
        
        <!-- Tab 2 start -->
        <div class="tab-content" data-tabs="section-mobile">
            <h3>手機版</h3>

            <div class="col-1-mobile-wrap">
                <div class="device-wrap mobile-dev-wrap" style="--device-w:375; --device-h:667;">
                    <div class="dev-outter-wrap">
                        <div class="dev-inner-wrap">                
                            <img src="@/assets/images/work/andmker_ebook/mobile_index_375.png" />
                        </div>
                    </div>
                </div>
            </div>

            <h4>Modal</h4>
            <div class="col-2-mobile-wrap">
                <div class="device-wrap mobile-dev-wrap" style="--device-w:375; --device-h:667;">
                    <div class="dev-outter-wrap">
                        <div class="dev-inner-wrap">                
                            <img src="@/assets/images/work/andmker_ebook/mobile_modal_375.png" />
                        </div>
                    </div>
                </div>
                <div class="device-wrap mobile-dev-wrap" style="--device-w:375; --device-h:667;">
                    <div class="dev-outter-wrap">
                        <div class="dev-inner-wrap">                
                            <img src="@/assets/images/work/andmker_ebook/mobile_success_375.png" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- Tab 2 end -->
    </div>
  </div>
</template>

<script>
// import jquery
import $ from "jquery";
window.$ = $;

export default {
    name: 'FemAndmakersEbook',
    mounted() {
      /* if not mac, hide scroll */
      this.isMac = navigator.platform.toUpperCase().indexOf('MAC') >= 0; // not found = -1
      let $dev_outter_wrap = $('.dev-outter-wrap');
      if ( !this.isMac && $dev_outter_wrap.length > 0 ) {
        $dev_outter_wrap.addClass('hide-scroll');
      }

      /* tabs feature */
      $('.tab-nav').on('click', function(){
        let tab_panel = $(this).parent('.tab-nav-wrap').attr('data-panel');
        
        let tab = $(this).attr('data-tabs');
        $(this).siblings('.active').removeClass('active');
        $(this).addClass('active');
    
        let $tab_content_wrap = $('.tab-content-wrap').filter(`[data-panel="${tab_panel}"]`);
        $tab_content_wrap.find('.active').removeClass('active');
        $tab_content_wrap.find(`[data-tabs="${tab}"]`).addClass('active');

        $('html, body').animate({
            'scrollTop': $(this).offset().top - 70
        }, 300);
      });
    }
}
</script>

